import { 
  PRODUCT_LIST_REQUEST, 
  PRODUCT_LIST_SUCCESS, 
  PRODUCT_LIST_FAIL,
  
  PRODUCT_DETAILS_REQUEST,
  PRODUCT_DETAILS_SUCCESS,
  PRODUCT_DETAILS_FAIL,
  
  PRODUCT_DELETE_REQUEST,
  PRODUCT_DELETE_SUCCESS,
  PRODUCT_DELETE_FAIL,
  
  PRODUCT_CREATE_REQUEST,
  PRODUCT_CREATE_SUCCESS,
  PRODUCT_CREATE_FAIL,
  PRODUCT_CREATE_RESET,
  
  PRODUCT_UPDATE_REQUEST,
  PRODUCT_UPDATE_SUCCESS,
  PRODUCT_UPDATE_FAIL,
  PRODUCT_UPDATE_RESET,
  
  PRODUCT_IMAGE_UPLOAD_REQUEST,
  PRODUCT_IMAGE_UPLOAD_SUCCESS,
  PRODUCT_IMAGE_UPLOAD_FAIL,
  PRODUCT_IMAGE_UPLOAD_RESET,
  
  PRODUCT_REVIEW_CREATE_REQUEST,
  PRODUCT_REVIEW_CREATE_SUCCESS,
  PRODUCT_REVIEW_CREATE_FAIL,
  PRODUCT_REVIEW_CREATE_RESET,

  PRODUCT_TOP_REQUEST, 
  PRODUCT_TOP_SUCCESS, 
  PRODUCT_TOP_FAIL,
} from '../constants/productConstants'


export const productListReducer = (state = { products: [] }, action) => {
  switch(action.type){
    case PRODUCT_LIST_REQUEST: return { loading:true, products:[] }
    case PRODUCT_LIST_SUCCESS: return { 
      loading:false, 
      products: action.payload.results, 
      page: action.payload.pagination.page,
      next: action.payload.pagination.next,
      previous: action.payload.pagination.previous,
      totalPages: action.payload.pagination.totalPages,
      count: action.payload.pagination.count,
    }
    case PRODUCT_LIST_FAIL: return { loading:false, error: action.payload }
    default: return state
  }
}


export const productTopReducer = (state = { products: [] }, action) => {
  switch(action.type){
    case PRODUCT_TOP_REQUEST: return { loading:true, products:[] }
    case PRODUCT_TOP_SUCCESS: return { loading:false, products: action.payload }
    case PRODUCT_TOP_FAIL: return { loading:false, error: action.payload }
    default: return state
  }
}


export const productDetailsReducer = (state = { product: { productreviews: [] } }, action) => {
  switch(action.type){
    case PRODUCT_DETAILS_REQUEST: return { loading:true, ...state }
    case PRODUCT_DETAILS_SUCCESS: return { loading:false, product: action.payload }
    case PRODUCT_DETAILS_FAIL: return { loading:false, error: action.payload }
    default: return state
  }
}


export const productDeleteReducer = (state = {}, action) => {
  switch(action.type){
    case PRODUCT_DELETE_REQUEST: return { loading: true }
    case PRODUCT_DELETE_SUCCESS: return { loading: false, success: true }
    case PRODUCT_DELETE_FAIL: return { loading: false, error: action.payload }
    default: return state
  }
}


export const productCreateReducer = (state = {}, action) => {
  switch(action.type){
    case PRODUCT_CREATE_REQUEST: return { loading: true }
    case PRODUCT_CREATE_SUCCESS: return { loading: false, success: true, product: action.payload }
    case PRODUCT_CREATE_FAIL: return { loading: false, error: action.payload }
    case PRODUCT_CREATE_RESET: return { product: {} }
    default: return state
  }
}


export const productUpdateReducer = (state = { product: {} }, action) => {
  switch(action.type){
    case PRODUCT_UPDATE_REQUEST: return { loading: true }
    case PRODUCT_UPDATE_SUCCESS: return { loading: false, success: true, product: action.payload }
    case PRODUCT_UPDATE_FAIL: return { loading: false, error: action.payload }
    case PRODUCT_UPDATE_RESET: return { product: {} }
    default: return state
  }
}


export const productImageUploadReducer = (state = { }, action) => {
  switch (action.type) {
      case PRODUCT_IMAGE_UPLOAD_REQUEST: return { loading: true }
      case PRODUCT_IMAGE_UPLOAD_SUCCESS: return { loading: false, success: true, image: action.payload }
      case PRODUCT_IMAGE_UPLOAD_FAIL: return { loading: false, error: action.payload }
      case PRODUCT_IMAGE_UPLOAD_RESET: return {}
      default: return state
  }
}


export const productReviewCreateReducer = (state = {}, action) => {
  switch(action.type){
    case PRODUCT_REVIEW_CREATE_REQUEST: return { loading: true }
    case PRODUCT_REVIEW_CREATE_SUCCESS: return { loading: false, success: true }
    case PRODUCT_REVIEW_CREATE_FAIL: return { loading: false, error: action.payload }
    case PRODUCT_REVIEW_CREATE_RESET: return { }
    default: return state
  }
}
